import type { LeftToolbarProps } from './types';
import { Block } from '@ali/4ever-cangjie';

const PREVENT_MOUSE_MOVE = Symbol('PREVENT_MOUSE_MOVE');
const CLEAR_PREVENT = Symbol('CLEAR_PREVENT');
const UPDATE_HOVER_BLOCK = Symbol('UPDATE_HOVER_BLOCK');

export function preventMouseMove() {
  return {
    type: PREVENT_MOUSE_MOVE,
  };
}

export function clearPrevent() {
  return {
    type: CLEAR_PREVENT,
  };
}

export function updateHoverBlock(payload?: Block) {
  return {
    type: UPDATE_HOVER_BLOCK,
    payload,
  };
}

export default function leftToolbarReducer(data: LeftToolbarProps, action): LeftToolbarProps {
  const { type, payload } = action;
  const { block, ...rest } = data;
  switch (type) {
    case PREVENT_MOUSE_MOVE:
      return {
        ...data,
        preventMouseMove: true,
      };
    case UPDATE_HOVER_BLOCK:
      if (!payload) {
        return rest;
      }
      return {
        ...rest,
        block: payload,
      };
    case CLEAR_PREVENT:
      return {
        ...data,
        preventMouseMove: false,
      };
    default:
      return data;
  }
}
